<template>
  <div>
    <div id="chatview">
      <transition name="bounce">
        <div>
          <div class="chat-header">
            <h3>Chatly</h3>
            <!-- <i class="fa fa-times" @click="showPop = false"></i> -->
          </div>
          <div ref="chatbody" class="chat-body" id="chat-body">
            <div v-if="isLoading" class="loading-div"></div>
            <!-- Line loader for chat timeout -->
            <!-- <div class="line-loader" :style="{ width: loaderWidth + '%' }"></div> -->
            <div class="message-box">
              <v-list item-props lines="two">
                <SingleChat
                  :convo="convo"
                  v-for="convo in sortedConversations"
                  :key="convo.text"
                  :agent="convo.agent"
                  :user="user"
                  @updateUnreadMsg="updateUnreadMsg"
                  class="chat-transition"
                />
              </v-list>
            </div>
          </div>

          <div class="chat-footer">
            <v-btn
              @click="$emit('createNewConvo')"
              class="start"
              :aria-details="getCount"
              getCount
              v-if="!getCount && !isLoading"
            >
              Start Conversation
            </v-btn>
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
import SingleChat from "../components/singleChat.vue";
import { createConversationId, getLastChat } from "@/utils/firbase_utils";
import io from "socket.io-client";
import { Timestamp } from "firebase/firestore";
export default {
  data() {
    return {
      socketMainpage: null,
      chatActive: false,
      isLoading: false,
      conversations: [],
    };
  },
  props: ["showPop", "addConversation", "user", "createNewConvo"],
  components: {
    SingleChat,
  },
  methods: {
    show() {
      this.$emit("showPoper");
    },

    updateUnreadMsg() {
      this.$emit("updateUnreadMsg", 1);
    },

    async saveConversationId(conversationId) {
      // Check if conversation ID has expired

      const timestamp = Date.now();

      const conversationData = {
        fetchState: "partial",
        read: true,
        expire: timestamp,
        ended: false,
      };
      this.addConversation(conversationId, conversationData);

      // localStorage.setItem("conversationId", conversationData);
    },

    handleNewMessage(newMessage) {
      console.log(newMessage.createdAt, "time check");
      const conversationIndex = this.conversations.findIndex(
        (c) => c.conversationId === newMessage.conversationId
      );
      if (conversationIndex > -1) {
        // Update the conversation with the new message text
        const conversation = this.conversations[conversationIndex];
        conversation.text = newMessage.text;
        conversation.userNewmessage = true;
        conversation.createdAt = newMessage.createdAt;
        this.conversations.splice(conversationIndex, 1, conversation);

        // Move the conversation to the top of the list
      }
    },

    async getAllInfo(conversation) {
      const result = await getLastChat(conversation);

      let userNewmessage = (await result.parentData.userNewmessage) || false;
      let lastMsg = await result.lastChatData;
      if (result.lastChatData === null) {
        lastMsg = {
          text: "hello message",
          user: "Bot",
          createdAt: Timestamp.now(),
        };
      }
      let ended;
      if (result.parentData.hasOwnProperty("ended")) {
        ended = result.parentData.ended;
      } else {
        ended = false;
      }

      return {
        conversationId: conversation,
        text: lastMsg.text,
        user: lastMsg.user,
        agent: result.parentData.agent,
        ended,
        userNewmessage: userNewmessage,
        createdAt: lastMsg.createdAt,
      };
    },

    isConversationIdExpired() {
      const conversationId = localStorage.getItem("conversationId");
      if (conversationId) {
        const conversationTimestamp = parseInt(conversationId.split("-")[1]);
        const now = Date.now();
        const twoDaysInMilliseconds = 7 * 24 * 60 * 60 * 1000; // 2 days in milliseconds
        return now - conversationTimestamp > twoDaysInMilliseconds;
      }
      return true; // conversation ID not found or invalid
    },
  },

  async created() {
    this.isLoading = true;
    this.socketMainpage = io("http://localhost:3000/");
    this.socketMainpage.on("connect", () => {
      console.log("Connected to server");
    });

    this.socketMainpage.on("newMessage", (message) => {
      message.createdAt = Timestamp.now();
      console.log(message.conversationId, this.user, "the new message");
      this.handleNewMessage(message);
    });

    const savedState = JSON.parse(localStorage.getItem("appState"));

    const conversationsById = savedState.conversations.byId;

    const valuesArray = [];

    for (const id in conversationsById) {
      const obj = { ...conversationsById[id], id };
      valuesArray.push(obj);
    }

    const reversedArray = valuesArray.reverse(); // reverse the order of the array

    const firstThree = reversedArray.slice(0, 3); // get the first three elements of the reversed array

    console.log(firstThree, "data i want");

    for (let i = 0; i < firstThree.length; i++) {
      const element = firstThree[i];
      let convoInfo = await this.getAllInfo(element.id);

      this.conversations = [...this.conversations, convoInfo];
    }

    if (firstThree.length > 0) {
      this.socketMainpage.emit("join", {
        id: firstThree[0].id,
        user: this.user,
      });
    }

    console.log(this.conversations, "data i want");

    this.isLoading = false;
  },
  computed: {
    sortedConversations() {
      return this.conversations.slice().sort((a, b) => {
        return b.createdAt.seconds - a.createdAt.seconds;
      });
    },

    getCount() {
      const endedCount = this.conversations.reduce((count, message) => {
        return count + (!message.ended ? 1 : 0);
      }, 0);
      console.log("computed", endedCount);

      return endedCount > 0;
    },
  },
};
</script>

<style>
