<template>
  <div>
    <div v-show="!feedback" id="chatview">
      <transition name="bounce">
        <div>
          <div class="chat-header">
            <v-icon
              style="cursor: pointer"
              @click="$emit('goBack')"
              start
              icon="mdi-arrow-left"
            ></v-icon>
            <h3>Chatly {{ agent }}</h3>
            <NewImage v-if="agent !== null" :name="botName" size="50" />
            <v-btn
              v-if="!ended && ended !== null"
              color="green"
              @click="showFeeback"
              >end Convo</v-btn
            >
          </div>
          <div
            v-if="showLoader"
            class="line-loader"
            :style="{ width: loaderWidth + '%' }"
          ></div>
          <div ref="chatbody" class="chat-body" id="chat-body">
            <div v-if="isLoading" class="loading-div"></div>
            <!-- Line loader for chat timeout -->
            <!-- <div class="line-loader" :style="{ width: loaderWidth + '%' }"></div> -->
            <div class="message-box">
              <div
                v-for="(message, index) in messages"
                :key="index"
                :value="user"
                :style="[
                  !checkNxt(message, index) ? 'margin-bottom: 6px;' : '',
                ]"
                :class="[
                  message.user === user
                    ? 'message-box__item outgoing'
                    : message.user === 'user'
                    ? 'message-box__item outgoing'
                    : 'message-box__item incoming',
                ]"
              >
                <div v-if="message.type === 'text'">
                  <div v-show="checkPrev(message, index)" class="name">
                    <NewImage
                      v-if="message.user !== user"
                      :name="message.user == 'bot' ? botName : message.user"
                      :size="20"
                    />
                    {{
                      capitalizeName(
                        message.user == "bot"
                          ? botName
                          : message.user === user
                          ? "you"
                          : message.user
                      )
                    }}
                  </div>

                  <div
                    :class="[
                      message.user === user
                        ? 'user-text'
                        : message.user === 'user'
                        ? 'user-text'
                        : 'bot-text',
                    ]"
                  >
                    <span v-html="renderMessage(message.text)"></span>
                  </div>
                </div>
                <div v-else-if="message.type === 'option'">
                  <div
                    v-for="(options, index) in message.option"
                    :key="index"
                    style="margin-bottom: 5px"
                  >
                    <input
                      type="checkbox"
                      :name="`name${index}`"
                      :value="options.text"
                      :id="index"
                    />
                    <label
                      :for="index"
                      class="md-chip md-chip-clickable md-chip-hover"
                      @click="clickSelect(options, message)"
                    >
                      {{ options.text }}</label
                    >
                  </div>
                </div>
                <div v-if="message.type === 'joined'" class="just-joined">
                  <span class="join-message"
                    >{{ message.text }} Just joined chat</span
                  >
                </div>
              </div>

              <div style="text-align: left; margin-bottom: 15px">
                <div v-if="isTyping || loading" id="loading-bubble">
                  <div class="spinner">
                    <div class="bounce1"></div>
                    <div class="bounce2"></div>
                    <div class="bounce3"></div>
                  </div>
                  <!-- <div>{{ typingMsg }}</div> -->
                </div>
              </div>
            </div>
          </div>

          <div v-if="!ended">
            <div
              v-if="!automated"
              class="chat-footer"
              :style="[ended ? 'display: block' : '']"
            >
              <textarea
                rows="1"
                ref="message"
                :disabled="automated"
                v-model="newMessage"
                @keyup.enter="sendMessage"
                @keydown.shift.enter.prevent="insertNewLine"
                @keyup="onTyping"
                type="text"
                placeholder="Type a message..."
                style="max-height: calc(3 * (1.5em + 2px))"
              ></textarea>
              <button :disabled="newMessage === ''" @click="sendMessage">
                Send
              </button>
            </div>
          </div>

          <div
            v-else
            class="chat-footer"
            :style="[ended ? 'display: block' : '']"
          >
            <div
              style="
                margin: '24px auto 12px';
                text-align: 'center';
                font-size: '14px';
                line-height: '21px';
                padding-top: '0px';
                color: 'rgb(115, 115, 115)';
              "
            >
              Your conversation has ended
            </div>
            <button @click="$emit('createNewConvo')" class="start">
              Start Conversation
            </button>
          </div>
        </div>
      </transition>
    </div>
    <feedBack
      @getConversationBy="getConversationBy"
      @updateConversation="updateConversation"
      v-show="feedback"
      :conversationId="conversationId"
      @goBack="feedback = false"
      @goBackTo="$emit('goBackTo')"
    />
  </div>
</template>

<script>
// import incoming from "../../assets/incoming.mp3";
// import outgoing from "../../assets/outgoing.mp3";
var incoming = document.querySelector('audio[src$="incoming.mp3"]');
var outgoing = document.querySelector('audio[src$="outgoing.mp3"]');
import io from "socket.io-client";
import {
  readMessage,
  getConvoChats,
  saveChats,
  setStepConversation,
  getConversation,
} from "@/utils/firbase_utils";

import NewImage from "../../components/NewImage.vue";
import feedBack from "../../components/test/feedBack.vue";
import axios from "axios";

export default {
  name: "chatView",
  data() {
    return {
      feedback: false,
      socket: null,
      automated: true,
      loading: false,
      showErrorDialog: true,
      isLoading: false,
      errorMessage: "Error Message",
      showChat: false,
      agent: null,
      ip: "",
      newMessage: "",
      // conversationId: "",
      isTyping: false,
      joined: false,
      typingMsg: "",
      step: 0,
      messages: [],
      chatTimeout: null,
      chatTimeoutDuration: 300000, // 5 minutes
      chatStartTime: null,
      loaderWidth: 0,
      intervalIds: [],
      showLoader: false,
      convoEnded: false,
      isBotEnabled: true,
      ended: null,
      flow: [
        {
          step: 0,
          next: true,
          auto: true,
          messages: [
            {
              text: "Hi! I'm glad to assist you today. Let's know each other:",
              user: "bot",
              type: "text",
            },
            {
              text: "Do you have an order already?",
              user: "bot",
              type: "text",
            },
            {
              user: "user",
              option: [
                {
                  id: 1,
                  text: "Yes",
                },
                {
                  id: 2,
                  text: "No",
                },
              ],
              type: "option",
            },
          ],
        },
        {
          step: 1,
          next: true,
          auto: true,
          messages: [
            {
              text: "Please select what you want to do with the order",
              user: "bot",
              type: "text",
            },
            {
              user: "user",
              option: [
                {
                  id: 1.1,
                  text: "i want to make a return/swap",
                },
                {
                  id: 1.2,
                  text: "I want to know the order status",
                },
                {
                  id: 1.3,
                  text: "Others",
                },
              ],

              type: "option",
            },
          ],
        },
        {
          step: 1.1,
          next: "end",
          auto: false,
          messages: [
            {
              text: "You can easly Return or swap an order here: <a href='https://site.com/return' target='_blank'>https://site.com/return</a> ",
              user: "bot",
              type: "text",
            },
          ],
        },
        {
          step: 1.2,
          next: "end",
          auto: false,
          messages: [
            {
              text: "You can track your order here: <a href='https://site.com/tracking' target='_blank'>https://site.com/tracking</a>",
              user: "bot",
              type: "text",
            },
          ],
        },
        {
          step: 1.3,
          next: "end",
          auto: false,
          messages: [
            {
              text: "Tell us how else can we help?",
              user: "bot",
              type: "text",
            },
          ],
        },
        {
          step: 2,
          next: true,
          auto: true,
          messages: [
            {
              text: "What would you like to do?",
              user: "bot",
              type: "text",
            },
            {
              user: "user",
              option: [
                {
                  id: 2.1,
                  text: "Place a new order",
                },
                {
                  id: 2.2,
                  text: "I have a question",
                },
                {
                  id: 2.3,
                  text: "Others",
                },
              ],
              type: "option",
            },
          ],
        },
        {
          step: 2.1,
          next: "end",
          auto: false,
          messages: [
            {
              text: "What products do you want?",
              user: "bot",
              type: "text",
            },
          ],
        },
        {
          step: 2.2,
          next: "end",
          auto: false,
          messages: [
            {
              text: "You can ask any question, we are here for you",
              user: "bot",
              type: "text",
            },
          ],
        },
        {
          step: 2.3,
          next: "end",
          auto: false,
          messages: [
            {
              text: "What other thing do you want us to do for you?",
              user: "bot",
              type: "text",
            },
          ],
        },
      ],
      newMessage: "",
    };
  },
  components: {
    NewImage,
    feedBack,
  },
  props: [
    "addConversation",
    "user",
    "updateConversation",
    "getConversationBy",
    "botName",
    "createNewConvo",
    "conversationId",
  ],
  methods: {
    insertNewLine(event) {
      if (event.shiftKey) {
        const textarea = this.$refs.message;
        const startPos = textarea.selectionStart;
        const endPos = textarea.selectionEnd;
        const currentValue = this.newMessage;
        this.newMessage = `${currentValue.substring(
          0,
          startPos
        )}\n${currentValue.substring(endPos)}`;
        // prevent the default behavior of inserting a new line
        event.preventDefault();
      }
    },
    showFeeback() {
      this.feedback = true;
    },

    async joinConversation(conversationId) {
      if (!this.ended) {
        this.socket.emit("join", { id: conversationId, user: this.user });
        // console.log(`${this.user} just joined conversation ${conversationId}`);
        this.joined = true;
      }
    },

    async sendMessage() {
      if (this.newMessage === "") {
        return;
      }
      const message = { text: this.newMessage };
      // const response = await axios.post('/api/messages', { conversationId: this.conversationId, message });
      await this.socket.emit("message", {
        conversationId: this.conversationId,
        type: "text",
        username: this.user,
        message: message,
        isUser: true,
      });

      this.socket.emit("typing", {
        status: false,
        user: this.user,
        id: this.conversationId,
      });
      this.chatStartTime = Date.now();
      localStorage.setItem("chatStarted", true);
      localStorage.setItem(
        `chatStartTime-${this.conversationId}`,
        this.chatStartTime
      );
      this.resetChatTimeout();
      this.newMessage = "";
    },

    onTyping() {
      if (!this.isTyping) {
        this.socket.emit("typing", {
          status: true,
          user: this.user,
          id: this.conversationId,
        });

        setTimeout(() => {
          this.socket.emit("typing", {
            status: false,
            user: this.user,
            id: this.conversationId,
          });
        }, 2000);
      }
    },

    checkPrev(object, index) {
      if (index === 0) {
        return true;
      }
      if (this.messages[index - 1].user === object.user) {
        return false;
      } else {
        return true;
      }
    },

    checkNxt(object, index) {
      if (index === 0) {
        return true;
      }
      const lastIndex = this.messages.length - 1;
      const currentIndex = this.messages.indexOf(object);
      if (currentIndex === lastIndex) {
        return true;
      }
      if (this.messages[index + 1].user === object.user) {
        return false;
      } else {
        return true;
      }
    },

    async newMessageSound(sound) {
      // var audio = new Audio(sound);
      sound.play();
    },

    async clickSelect(msg, fullmsg) {
      // console.log(msg, "the message");
      this.newMessageSound(outgoing);
      this.loading = true;
      this.messages.pop();
      const newMessage = {
        text: msg.text,
        user: "user",
        type: "text",
      };
      this.messages = [...this.messages, newMessage];

      this.loading = true;
      await this.checkCase(this.step, true, msg, false);
      await this.checkCase(this.step, false, msg, false);
      this.newMessageSound(incoming);
      this.chatToChatbottom();
      this.loading = false;
    },

    async chatToChatbottom() {
      if (document.querySelector("#chat-body")) {
        // console.log("scroll");
        document.querySelector("#chat-body").scrollTop =
          document.querySelector("#chat-body").scrollHeight;
      }
    },

    async saveMessage(msg, id) {
      const newMessage = {
        text: msg.text,
        type: msg.type,
        user: msg.user,
        isUser: true,
      };
      await saveChats(newMessage, id);
    },

    requestAssistance() {
      if (!this.agent && !this.ended) {
        const data = { conversationId: this.conversationId, userId: this.user };
        this.socket.emit("requestAssistance", data);
        // Check if chat session has already started
        const chatStarted = localStorage.getItem("chatStarted");
        this.showLoader = true;
      }
    },

    async stopAutomate() {
      // const botMsg = {
      //   text: "Great  A member of our team will be in touch shortly. what can we do for you?",
      //   user: "bot",
      //   type: "text",
      //   isUser: true,
      // };
      // this.messages.push(botMsg);
      // this.saveMessage(botMsg, this.conversationId);
      this.joinConversation(this.conversationId);
      this.automated = false;
      this.chatToChatbottom();
      this.requestAssistance();
    },

    isSocketInConversation() {
      const conversationRoom = `conversation:${this.conversationId}`;

      this.socket.emit(
        "isSocketInConversation",
        conversationRoom,
        (isJoined) => {
          if (isJoined) {
            // console.log(
            //   `Socket has joined conversation ${this.conversationId}`
            // );
          } else {
            // console.log(
            //   `Socket has not joined conversation ${this.conversationId}`
            // );
            this.joinConversation(this.conversationId);
          }
        }
      );
    },

    async checkCase(step, save, msg, first) {
      // find the object that has the step give in the function value
      // console.log(save, "the messages", step, first);
      let valueStep = this.flow.find((obj) => obj.step === step);
      // console.log(save, "the messages", valueStep);
      if (valueStep.next === "end" && first) {
        // console.log("the end of it ");
        if (!valueStep.auto) {
          this.isSocketInConversation();
          let intervalIdReq;

          // Start the interval
          // intervalIdReq = setInterval(() => {
          //   // Call your method here
          //   this.requestAssistance();
          // }, 4000);

          this.automated = false;
        }
        this.chatToChatbottom();
        return;
      }

      if (save) {
        // console.log(valueStep);
        valueStep.messages.pop();
        // loop through and  save user options
        for (let index = 0; index < valueStep.messages.length; index++) {
          const element = valueStep.messages[index];
          element.isUser = true;
          await this.saveMessage(element, this.conversationId);
        }

        let userMsg = {
          text: msg.text,
          user: this.user,
          type: "text",
          isUser: true,
        };
        await this.saveMessage(userMsg, this.conversationId);
        await setStepConversation(this.conversationId, msg.id);
        this.step = msg.id;
      }

      // No check and update next question for automated process
      if (!save) {
        for (let index = 0; index < valueStep.messages.length; index++) {
          const element = valueStep.messages[index];
          element.isUser = true;
          // console.log("valueStep", valueStep);
          this.messages.push(element);
          if (valueStep.next === "end" || !valueStep.auto) {
            await this.saveMessage(element, this.conversationId);
            // console.log("saving ending ");
          }
        }
        if (valueStep.next === "end" && !valueStep.auto) {
          await this.stopAutomate();
          await this.sendToBot("point 3");
        }
      }
      // console.log(valueStep.next, save, "the values", step);
    },

    async lastMessageIsBot() {
      const lastMessage = this.messages[this.messages.length - 1];

      if (lastMessage.user === "bot") {
        // console.log(lastMessage, "last message");
      }
      return lastMessage && lastMessage.user === "bot";
    },

    startChatTimeout() {
      // Calculate remaining time based on elapsed time
      const elapsedTime = Date.now() - this.chatStartTime;
      const remainingTime = Math.max(this.chatTimeoutDuration - elapsedTime, 0);

      // Start the chat timeout timer
      this.chatTimeout = setTimeout(() => {
        // End the chat session on timeout
        if (!this.ended) {
          this.socket.emit("timeUp", this.conversationId);
        }

        this.expiredConvId(true);
      }, remainingTime);

      // Update the line loader every second
      const intervalId = setInterval(() => {
        const elapsedTime = Date.now() - this.chatStartTime;
        const remainingTime = Math.max(
          this.chatTimeoutDuration - elapsedTime,
          0
        );
        const remainingPercentage =
          (remainingTime / this.chatTimeoutDuration) * 100;
        this.loaderWidth = 100 - remainingPercentage;
      }, 1000);

      this.intervalIds.push(intervalId);
    },

    resetChatTimeout() {
      // Reset the chat timeout timer
      clearTimeout(this.chatTimeout);
      if (!this.chatStartTime) {
        this.chatStartTime = Date.now();
      }
      this.startChatTimeout();

      // Clear the line loader interval
      clearInterval(this.intervalIds.pop());
      this.loaderWidth = 0;
    },

    getConversationById(conversationId) {
      const savedState = JSON.parse(localStorage.getItem("appState")) || {};
      const conversation =
        savedState.conversations?.byId[conversationId] || null;
      return conversation;
    },

    getConversationIdFromLocalStorage() {
      const conversationData = localStorage.getItem("conversationId");
      if (conversationData) {
        const conversationId = conversationData.split("-")[0];
        return conversationId;
      }
      return null; // conversation ID not found or invalid
    },

    saveConversationId(conversationId) {
      // Check if conversation ID has expired

      const timestamp = Date.now();

      const conversationData = {
        fetchState: "partial",
        read: true,
        expire: timestamp,
      };
      this.addConversation(conversationId, conversationData);

      // localStorage.setItem("conversationId", conversationData);
    },

    isConversationIdExpired() {
      // const conversationId = localStorage.getItem("conversationId");
      const convo = this.conversationId || this.conversationId;
      const expire = this.getConversationById(convo).expire;
      if (convo) {
        const conversationTimestamp = parseInt(expire);
        const now = Date.now();
        const twoDaysInMilliseconds = 7 * 24 * 60 * 60 * 1000; // 2 days in milliseconds
        return now - conversationTimestamp > twoDaysInMilliseconds;
      }
      return true; // conversation ID not found or invalid
    },

    async expiredConvId(value) {
      if (this.isConversationIdExpired() || value) {
        // this.ended = true;
      }
    },

    async getConversationId() {
      let localChatId = this.conversationId || null;
      if (localChatId === null) {
        this.$router.push({ name: "home" });
      }

      return localChatId;
    },

    capitalizeName(name) {
      // console.log(name, "the name");
      return name.charAt(0).toUpperCase() + name.slice(1);
    },

    iniStartTime() {
      this.chatStartTime = localStorage.getItem(
        `chatStartTime-${this.conversationId}`
      );
      if (this.chatStartTime) {
        // Restore chat start time from local storage

        this.showLoader = true;
        this.startChatTimeout();
      }
      // Set the chatStarted flag and chat start time in local storage
      if (!this.chatStartTime) {
        this.chatStartTime = Date.now();
        localStorage.setItem(
          `chatStartTime-${this.conversationId}`,
          this.chatStartTime
        );
      }
    },

    async updateConversation(conversationId, conversationData) {
      const data = JSON.parse(localStorage.getItem("appState"));
      const newState = {
        ...data,
        conversations: {
          ...data.conversations,
          byId: {
            ...data.conversations.byId,
            [conversationId]: {
              ...data.conversations.byId[conversationId],
              ...conversationData,
            },
          },
        },
      };
      //   this.updateState(newState);
      // console.log(newState, "updateing feedback");
      localStorage.setItem("appState", JSON.stringify(newState));
    },

    getConversationBy(conversationId) {
      const data = JSON.parse(localStorage.getItem("appState"));
      const conversations = data.conversations.byId;
      if (conversations.hasOwnProperty(conversationId)) {
        return conversations[conversationId];
      } else {
        return null;
      }
    },

    async sendToBot(point) {
      // console.log("joshijax", point);
      this.sending = true;
      this.isTyping = true;
      let messageStrings = this.messages.map((msg) => "\n" + msg.text);
      let isbot = await this.lastMessageIsBot();

      if (isbot || this.automated) {
        this.sending = false;
        this.isTyping = false;
        return;
      }
      let allMessageText = " \nhello\n " + messageStrings.join("");
      // console.log(allMessageText, "TEXT", messageStrings);
      this.inputText = "";
      try {
        // console.log(allMessageText, "TEXT2");
        const response = await axios({
          method: "post",
          url: "http://127.0.0.1:8000/answer-me/",

          data: {
            prompt: allMessageText,
          },
        });

        // console.log("JOSHIJAX", typeof response.data.response);

        await this.socket.emit("message", {
          conversationId: this.conversationId,
          type: "text",
          username: "bot",
          message: { text: response.data.response },
          isUser: false,
        });
        this.isTyping = false;
      } catch (error) {
        // console.error(error);
        this.isTyping = false;
      }
      this.sending = false;
    },

    messageEndsWithGoodbye(statement) {
      if (!statement) {
        return false;
      }
      // Define common keywords and phrases that indicate a goodbye message
      const goodbyeKeywords = [
        "goodbye",
        "bye",
        "farewell",
        "see you",
        "take care",
        "have a nice",
        "later",
      ];

      // Define regular expressions to match common patterns in goodbye messages
      const patterns = [
        /\b(thanks|thank you)\b/gi,
        /\b(good night|night)\b/gi,
        /\b(talk to you|chat with you)\b/gi,
        /\b(see you|cu|ttyl|ttys)\b/gi,
        /\b(be safe)\b/gi,
        /\b(have a great|have a nice)\b/gi,
        /\b(take care)\b/gi,
        /\b(sleep well)\b/gi,
        /\b(catch you later|laters)\b/gi,
      ];

      // Check if the statement contains a goodbye keyword
      for (const keyword of goodbyeKeywords) {
        if (statement.toLowerCase().includes(keyword)) {
          return true;
        }
      }

      // Check if the statement matches any of the common patterns
      for (const pattern of patterns) {
        if (pattern.test(statement)) {
          return true;
        }
      }

      // If no keywords or patterns were found, assume it's not a goodbye message
      return false;
    },

    renderMessage(message) {
      // Detect links in the message and render them as clickable links
      const linkRegex = /(https?|ftp):\/\/[^\s]+/gi;

      if (this.isLink(message)) {
        // Replace the link in the message with an anchor tag element
        message = message.replace(
          linkRegex,
          '<a href="$&" target="_blank">$&</a>'
        );
      }

      return message;
    },

    isLink(text) {
      // Match links that do not already have an anchor tag
      const linkRegex = /^(?!.*?<a\s).*?(https?|ftp):\/\/[^\s]+.*$/i;
      return linkRegex.test(text);
    },

    async iniChat() {
      // console.log("joshijaxmsg", this.botName);
      this.isLoading = true;
      let convoinfo = {};

      convoinfo = await getConversation(this.conversationId);
      this.agent = convoinfo.agent;

      this.messages = await getConvoChats(this.conversationId);

      // console.log("glory", this.messages);

      this.step = convoinfo.step;

      await this.expiredConvId(false);

      this.chatToChatbottom();

      this.iniStartTime();
      // await this.submitText();
      this.isLoading = false;
      if (convoinfo.hasOwnProperty("ended")) {
        this.ended = convoinfo.ended;
        let item = this.getConversationBy(this.conversationId);

        item.ended = convoinfo.ended;
        // console.log(item, "the item");
        await this.updateConversation(this.conversationId, item);
      } else {
        this.ended = false;
      }

      await this.checkCase(this.step, false, null, true);

      if (!this.ended && this.isBotEnabled && !this.automated) {
        let logivVal = await this.lastMessageIsBot();

        if (!logivVal) {
          this.sendToBot("point 2");
        }
      }
    },
  },

  async created() {
    // console.log("botName 2", this.botName);
    this.socket = io("http://localhost:3000/");
    this.socket.on("connect", () => {
      // console.log("Connected to server");

      this.socket.emit("userType", "customer");
    });

    // litsen to new

    this.socket.on("newMessage", async (message) => {
      console.log(message.user, this.user);
      if (message.user === this.user || message.user === "user") {
        if (this.user === message.user) {
        }
        let value = {
          text: message.text,
          user: message.user,
          type: "text",
        };
        console.log("sent out", value);
        await this.messages.push(value);
        // console.log(this.messages, "here final");
        await this.chatToChatbottom();
        await this.newMessageSound(outgoing);
        if (this.isBotEnabled) {
          this.sendToBot("point 1");
        }
      } else {
        console.log("recieved", message);
        this.messages.push(message);
        // console.log(this.messages, "here final");
        this.chatToChatbottom();
        this.newMessageSound(incoming);
        readMessage(this.conversationId);
      }
    });

    this.socket.on("joined", async (message) => {
      // console.log(
      //   this.user != message.user,
      //   this.user,
      //   message.user,
      //   "the data i need"
      // );

      if (this.user != message.user) {
        if (this.agent !== message.user) {
          this.messages.push(message);
          // this.saveMessage(message);
          await this.chatToChatbottom();
        }
      }
    });

    this.socket.on("typing", (isTyping) => {
      // console.log(isTyping);
      this.isTyping = isTyping.status;
      this.typingMsg = `${isTyping.user} is typing`;
      this.chatToChatbottom();
    });

    this.socket.on("requestAccepted", async (data) => {
      // console.log(data, "requestAccepted");
      this.agent = data.userId;
    });

    await this.iniChat();
  },

  beforeUnmount() {
    this.socket.off();
  },

  watch: {
    messages: {
      handler(newMessages) {
        const lastMessage = newMessages[newMessages.length - 1];
        if (
          lastMessage &&
          this.messageEndsWithGoodbye(lastMessage.text) &&
          this.ended !== null &&
          !this.ended
        ) {
          // console.log('Last message ended with "Goodbye"');

          setTimeout(() => {
            this.showFeeback();
            // console.log('Last message ended with "Goodbye"')
          }, 3000);
        }
      },
      deep: true,
    },

    $route: {
      async handler(to, from) {
        await this.iniChat();
        // if (to.params.id !== from.params.id) {

        // }
      },
      immediate: true,
    },
  },

  computed: {
    displayAgent() {
      return this.agent || "bot";
    },
    // ...other computed properties
  },
};
</script>
