import {
  createRouter,
  createWebHistory,
  createWebHashHistory,
} from "vue-router";
import chatmain from "../views/chatmain.vue";
import chatView from "../views/chatView.vue";
import feedbackView from "../views/feedbackView.vue";

const routes = [
  {
    path: "/",
    name: "home",
    component: chatmain,
  },
  {
    path: "/conversation/:id",
    name: "conversation",
    component: chatView,
  },
  {
    path: "/feedback/:id",
    name: "feedback",
    component: feedbackView,
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

router.afterEach((to, from) => {
  const appState = JSON.parse(localStorage.getItem("appState")) || {};
  console.log("thus joshua ikiah", appState);
  const newState = {
    ...appState,
    router: {
      location: {
        pathname: to.path,
        name: to.name,
        search: to.query,
        hash: to.hash,
        state: null,
        key: "",
      },
      action: to.name === from.name ? "REPLACE" : "PUSH",
      previousLocations: [
        {
          location: {
            pathname: from.path,
            search: from.query,
            hash: from.hash,
            state: null,
            key: "",
          },
          action: to.name === from.name ? "REPLACE" : "PUSH",
        },
      ],
    },
  };
  localStorage.setItem("appState", JSON.stringify(newState));
});

export default router;
