<template>
  <div id="chatview">
    <transition name="bounce">
      <div>
        <div class="chat-header">
          <v-icon
            style="cursor: pointer"
            @click="
              $router.push({
                name: 'conversation',
                params: { id: $route.params.id },
              })
            "
            start
            icon="mdi-arrow-left"
          ></v-icon>
          <h3>Chatly</h3>
        </div>

        <div ref="chatbody" class="chat-body" id="chat-body">
          <div v-if="isLoading" class="loading-div"></div>

          <div class="d-flex flex-wrap">
            <v-form ref="form">
              <h1>Feedback</h1>
              <v-btn-toggle v-model="selectedEmoji" mandatory>
                <v-btn
                  v-for="emoji in emojis"
                  :key="emoji.value"
                  :value="emoji.value"
                  class="ma-2 custom-btn"
                >
                  <span class="emoji">{{ emoji.label }}</span>
                  <v-tooltip top>
                    <span class="tooltip-value">{{ emoji.value }}</span>
                  </v-tooltip>
                </v-btn>
              </v-btn-toggle>
              <v-text-field
                v-model="comments"
                label="Additional Comments"
                :rules="rules.comments"
              ></v-text-field>
              <v-btn
                color="blue"
                @click="submitFeedback"
                :disabled="selectedEmoji === null"
                >Submit</v-btn
              >
            </v-form>
          </div>
        </div>

        <div class="chat-footer">
          <!-- <div
              style="
                margin: 24px auto 12px;
                text-align: center;
                font-size: 14px;
                line-height: 21px;
                padding-top: 0px;
                color: rgb(115, 115, 115);
              "
            >
              Your conversation has ended
            </div>
            <button @click="createNewConvo" class="start">
              Start Conversation
            </button> -->
        </div>
      </div>
    </transition>
    <ErrorDialog
      v-if="showErrorDialog"
      :error="errorMessage"
      @dismiss="hideErrorDialog"
    />
  </div>
</template>

<script>
import mainPage from "../components/mainPage.vue";
export default {
  data() {
    return {
      selectedEmoji: null,
      comments: "",
      isLoading: false,
      emojis: [
        { value: "bad", label: "" },
        { value: "fair", label: "" },
        { value: "good", label: "" },
        { value: "great", label: "" },
      ],
      rules: {
        comments: [
          (value) => !!value || "Comments are required",
          (value) =>
            value.length <= 1000 ||
            "Comments must be less than 1000 characters",
        ],
      },
    };
  },

  props: ["showPop", "showChat", "addConversation", "stateval", "user"],

  components: {
    mainPage,
  },

  methods: {
    async submitFeedback() {
      if (this.$refs.form.validate()) {
        console.log("the item");
        let item = this.getConversationBy(this.conversationId);
        item.ended = false;
        // console.log(item, "the item");
        this.updateConversation(this.conversationId, item);
      }
      // Code to submit feedback goes here
    },
  },

  created() {},
};
</script>
<style scoped>
.v-btn-toggle {
  display: flex;
  justify-content: center;
}

.custom-btn {
  font-size: 37px;
  padding: 16px;
}

.emoji {
  display: block;
}

.tooltip-value {
  font-size: 14px;
}

.v-btn-group--density-default.v-btn-group {
  height: 95px;
}
</style>
